<template>
  <div
    id="page-home-background-change"
    @click.stop
  >
    <!--  -->
    <Popup
      v-model="showPopup"
      position="bottom"
      closeable
      :close-on-click-overlay="false"
      @open="setFormValues(backgroundInfo)"
    >
      <!--  -->
      <div
        id="page-home-background-change-title"
        class="van-hairline--bottom"
      >
        
      </div>
      <!--  -->
      <Form
        id="page-home-background-change-from"
        @submit="onSubmitFormValues"
      >
        <!--  -->
        <Field
          label=""
        >
          <template #input>
            <RadioGroup
              v-model="formValues.bookmarkTitleColor"
              direction="horizontal"
            >
              <Radio
                v-for="item in bookmarkTitleColorList"
                :key="item.color"
                :name="item.color"
              >
                {{item.text}}
              </Radio>
            </RadioGroup>
          </template>
        </Field>
        <!--  -->
        <Field
          label=""
        >
          <template #input>
            <Uploader
              v-model="formValues.backgroundImageList"
              :max-count="1"
              :max-size="maxBgImgSize"
              :before-read="checkBgImgType"
              @oversize="onBgImgOversize"
            />
          </template>
        </Field>
        <!--  -->
        <div id="page-home-background-change-btns">
          <Button
            round
            block
            type="info"
            native-type="submit"
          >
            
          </Button>
        </div>
      </Form>
    </Popup>
  </div>
</template>

<style scoped>
#page-home-background-change-title {
  font-size: 16px;
  line-height: 54px;
  text-align: center;
}

#page-home-background-change-btns {
  padding: 24px 16px;
}
</style>

<script>
import { Button, Field, Form, Radio, RadioGroup, Popup, Toast, Uploader } from 'vant'

export default {
  name: 'PageHomeBackgroundChange',
  components: {
    Button,
    Field,
    Form,
    Radio,
    RadioGroup,
    Popup,
    Uploader
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    backgroundInfo: {
      type: Object,
      default () {
        return {
          bmTitleColor: '#000000',
          bgImgName: undefined,
          bgImgFile: undefined,
          bgImgDataURL: undefined
        }
      }
    }
  },
  data () {
    return {
      defatultBookmarkTitleColor: '#000000',
      formValues: {
        bookmarkTitleColor: '',
        backgroundImageList: []
      },
      bookmarkTitleColorList: [
        {
          color: '#000000',
          text: ''
        },
        {
          color: '#ffffff',
          text: ''
        }
      ],
      maxBgImgSize: 2 << 20
    }
  },
  computed: {
    showPopup: {
      get () {
        return this.show
      },
      set (value) {
        this.$emit('update:show', value)
      }
    }
  },
  methods: {
    // 
    setFormValues (bgInfo) {
      // 
      this.formValues.bookmarkTitleColor = bgInfo?.bmTitleColor ?? this.defatultBookmarkTitleColor
      // 
      if (bgInfo.bgImgFile) {
        const ImgFileItem = {
          file: bgInfo.bgImgFile,
          content: bgInfo.bgImgDataURL
        }
        this.formValues.backgroundImageList = [ImgFileItem]
      }
    },
    // 
    resetFormValues () {
      Object.assign(this.formValues, {
        bookmarkTitleColor: this.defatultBookmarkTitleColor,
        backgroundImageList: []
      })
    },
    // 
    checkBgImgType (file) {
      if (['image/jpeg', 'image/png'].includes(file.type)) {
        return true
      } else {
        Toast.fail('jpeg/jpg/png')
        return false
      }
    },
    // 
    onBgImgOversize () {
      Toast.fail(`${this.maxBgImgSize >> 20}MB`)
    },
    // 
    getImageNameByTimestamp (fileType) {
      return `${new Date().getTime()}.${fileType.split('/')[1]}`
    },
    // 
    onSubmitFormValues () {
      // 
      const bmTitleColor = this.formValues.bookmarkTitleColor
      // 
      const bgImgFile = this.formValues.backgroundImageList[0]
      // 
      const bgImgName = bgImgFile?.file ? this.getImageNameByTimestamp(bgImgFile.file.type) : undefined
      // 
      const BgInfo = {
        bmTitleColor: bmTitleColor,
        bgImgName: bgImgName,
        bgImgFile: bgImgFile?.file,
        bgImgDataURL: bgImgFile?.content
      }
      // 
      this.$emit('background-changed', BgInfo)
      // 
      this.showPopup = false
    }
  }
}
</script>
